.onboarding-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #1a0a25, #0e051b);
  color: white;
  padding: 60px 20px 120px 20px;
  overflow-x: hidden;
}

.onboarding-container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100vh - 180px);
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 80px;
  align-items: center;
  width: 100%;
}

.hero-text {
  max-width: 500px;
}

.hero-text h1 {
  font-size: 3.2rem;
  font-weight: 600;
  margin-bottom: 24px;
  line-height: 1.2;
  color: white;
}

.hero-text p {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 40px;
  line-height: 1.6;
}

.stats-row {
  display: flex;
  gap: 40px;
  margin-bottom: 40px;
}

.stat {
  display: flex;
  flex-direction: column;
}

.stat-number {
  font-size: 2rem;
  font-weight: 700;
  color: #8A2BE2;
  margin-bottom: 4px;
}

.stat-label {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.6);
  white-space: nowrap;
}

.get-started-btn {
  display: inline-flex;
  align-items: center;
  gap: 12px;
  background: #8A2BE2;
  color: white;
  border: none;
  padding: 16px 32px;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  margin-bottom: 24px;
}

.get-started-btn:hover {
  background: #9B59B6;
  transform: translateY(-1px);
}

.login-link {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.6);
}

.login-link span {
  color: #8A2BE2;
  cursor: pointer;
  text-decoration: underline;
}

.login-link span:hover {
  color: #9B59B6;
}

.hero-visual {
  display: flex;
  justify-content: center;
  align-items: center;
}

.adbox-mockup {
  position: relative;
}

.adbox-screen {
  width: 160px;
  height: 240px;
  background: #000;
  border-radius: 12px;
  border: 2px solid #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;
  color: #8A2BE2;
  font-size: 0.9rem;
  font-weight: 500;
}



/* Mobile Responsive */
@media (max-width: 768px) {
  .onboarding-page {
    padding: 40px 20px 120px 20px;
  }
  
  .onboarding-container {
    min-height: calc(100vh - 160px);
  }
  
  .hero-content {
    grid-template-columns: 1fr;
    gap: 40px;
    text-align: center;
  }
  
  .hero-text {
    max-width: 100%;
  }
  
  .hero-text h1 {
    font-size: 2.5rem;
  }
  
  .stats-row {
    justify-content: center;
    gap: 30px;
  }
  
  .stat-number {
    font-size: 1.8rem;
  }
  
  .adbox-screen {
    width: 140px;
    height: 200px;
  }
}

@media (max-width: 480px) {
  .onboarding-page {
    padding: 30px 15px 120px 15px;
  }
  
  .onboarding-container {
    min-height: calc(100vh - 150px);
  }
  
  .hero-content {
    gap: 30px;
  }
  
  .hero-text h1 {
    font-size: 2rem;
    line-height: 1.1;
  }
  
  .hero-text p {
    font-size: 1rem;
  }
  
  .stats-row {
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .stat-number {
    font-size: 1.6rem;
  }
  
  .stat-label {
    font-size: 0.8rem;
  }
  
  .get-started-btn {
    padding: 14px 28px;
    font-size: 0.9rem;
  }
  
  .adbox-screen {
    width: 120px;
    height: 180px;
  }
} 